<template>
  <div>
    <div class="page-title">
      <h3>Личная статистика игрока - {{$route.params.nick}}</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <Loader v-if="loading"/>
      <div v-else>
            <p>Общая статистика:</p>
      <v-table :data='n'>
        <thead slot="head">
          <v-th sortKey="playerName" defaultSort="desc">Ник</v-th>
          <v-th sortKey="totalPlayedGames" defaultSort="desc">Кол-во игр</v-th>
          <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
          <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
          <v-th sortKey="deaths" defaultSort="desc">Смертей</v-th>
          <v-th sortKey="kdRatio" defaultSort="desc">K/D</v-th>
          <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{displayData}">
        <tr v-for="row in displayData" :key="row.id">
          <td><router-link :to="{name:'Player', params:{nick: row.playerName}}">{{row.lastSquadPrefix + row.playerName}}</router-link></td>
          <td>{{row.totalPlayedGames}}</td>
          <td>{{row.kills}}</td>
          <td>{{row.teamkills}}</td>
          <td>{{row.deaths}}</td>
          <td>{{row.kdRatio}}</td>
          <td>{{row.totalScore}}</td>
        </tr>
          </tbody>
      </v-table>
      <p>Статистка по неделям:</p>
          <v-table :data='byWeeks'>
        <thead slot="head">
          <th>Даты</th>
          <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
          <v-th sortKey="kills">Убийств</v-th>
          <v-th sortKey="teamkills">Тимкиллов</v-th>
          <v-th sortKey="deaths">Смертей</v-th>
          <v-th sortKey="kdRatio">K/D</v-th>
          <v-th sortKey="totalScore">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{displayData}">
        <tr v-for="row in displayData" :key="row.id">
          <td>{{row.startDate}} - {{row.endDate}}</td>
          <td>{{row.totalPlayedGames}}</td>
          <td>{{row.kills}}</td>
          <td>{{row.teamkills}}</td>
          <td>{{row.deaths}}</td>
          <td>{{row.kdRatio}}</td>
          <td>{{row.totalScore}}</td>
        </tr>
          </tbody>
      </v-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data: () => ({
    n: [],
    byWeeks: [],
    loading: false,
  }),
  async mounted() {
      this.loading = true;
      await this.r();
      this.loading = false;

  },
  methods: {
    async refresh() {
      this.loading = true;
      await this.r()
      this.loading = false;
    },
    async r(){
    this.n = [];
    this.byWeeks = [];
    let r = await fetch("../stats.json");
    r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].playerName === this.$route.params.nick) {
          this.n.push(r.globalStatistics[key]);
          this.byWeeks = r.globalStatistics[key].byWeeks;
        }
      }
      for(let key in this.byWeeks){
          this.byWeeks[key].totalScore = (this.byWeeks[key].kills-this.byWeeks[key].teamkills/this.byWeeks[key].totalPlayedGames).toFixed(2);
          this.byWeeks[key].startDate = new Date(this.byWeeks[key].startDate).toLocaleString('ru', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
        this.byWeeks[key].endDate = new Date(this.byWeeks[key].endDate).toLocaleString('ru', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
      }
    }
  },
};
</script>
